#main_section {
  margin-left: 10px;
  font-size: 2em;
  width: 1200px;
}

.tlo {
  background-image: radial-gradient(circle at right, #fcd475, white);
}

.json {
  font-family:Consolas,Monaco,Lucida Console,Liberation Mono,DejaVu Sans Mono,Bitstream Vera Sans Mono,Courier New, monospace;
  height: 140px;
}

.wstep {
  background-repeat: no-repeat;
}

.prawa {
  text-align: center;
}

.nazwa_gminy {
  font-size: 2.8em;
}

.autor {
  text-align: right;
  font-weight: bold;
}

.noLabel {
  margin-top: 10px;
}

.zolty {
  background-color: #f9c00c;
}

#selektor-gminy, #selektor-wojewodztwa {
  margin-bottom: 10px;
}

.formularzContainer {
  position: relative;
  top: 0;
}

.formularz {
  position: fixed;
  top: 0;
  height: calc(100%);
  overflow-y: auto;
  background-color: #dddddd;
  padding: 5px;
}

.accordionContent {
  padding: 10px;
  background-color: #eeeeee;
}

.przycisk {
  background-color: #ffffff;
  margin-bottom: 10px;
}

input, textarea {
  margin-bottom: 10px;
}

.hint {
  font-size: 0.7em;
}

.ikonaWykresu {
  margin: 5px 5px 5px 5px;
  background-color: white;
  border-width: 3px;
}

.panelIkon {
  justify-content: center;
}

.odd {
  background-color: #dddddd;
}

.panelDodania {
  justify-content: center;
}

/* PieContainer - część jest domyślna*/

.pieContainer {
  padding: 30px;
  height: 1200px;
  position: relative;
}

.pieBackground {
  position: absolute;
  width: 1200px;
  height: 1200px;
  border-radius: 100%;
  box-shadow: 0 0 32px rgba(0,0,0,0.5);
}

.pie {
  position: absolute;
  width: 1200px;
  height: 1200px;
  border-radius: 100%;
  clip: rect(0px, 600px, 1200px, 0px);
}

.hold {
  position: absolute;
  width: 1200px;
  height: 1200px;
  border-radius: 100%;
  clip: rect(0px, 1200px, 1200px, 600px);
}

/* Zmniejsza legendę do rozsądnych rozmiarów */
.recharts-legend-item {
  line-height: 1.2;
  font-size: 0.8em;
  margin-right: 30px;
}

/* ==================================== */
/* ==========LEGACY==================== */
/* ==================================== */

#main_table {
  table-layout: fixed;
  width: 100%;
}
#main_table td {
  padding: 20px;
  text-align: center;
}
.dzial{
  font-size: 1.6em;
  color: black
}
.sekcja_dzial {
  font-size: 3em;
  color: black
}

.projekty, tr {
  border-collapse: collapse;
}

.projekty td {
  border: 1px solid black;
}
.wartosc {
  font-size: 3.2em;
  color: #ffffff;
}

.więcej {
  font-size: 0.8em;
  color: green;
}

.mniej {
  font-size: 0.8em;
  color: red;
}

#nazwa {
  text-align: center;
  font-size: 5em;
}

.chart {
  display: grid;
  grid-template-columns: 0.4fr 0.6fr;
  grid-gap: 20px;
}

.legend {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2px;
}